import React from 'react';

let Utils = require('../utils/utils');
require('styles/PicIndex.scss');

export default class PicIndex extends React.Component {
  picDress(e) {
  	var that = e.target;
  	Utils.dress(e, that);
  }

  render() {
  	//var url = Utils.serverName + 'search/picSearchGetThumbnail.shtml?picPath=' + this.props.pic.url + '&searchType=2';
  	var url = 'http://www.lacewang.com/pic/picThumb?picPath=' + this.props.pic.url + '&picType=2&size=300';
  	return (
		<div className="col-md-3 col-sm-4 col-xs-6 my-col pic item">
			<div className="thumbnail">
				<a href="javascript:;">
					<img src={url} alt=""/>
				</a>
		        <div className="caption">
			        <div className="price">
				        <span></span>
				        <strong>{this.props.pic.price}</strong>
			        </div>
			        <div className="no">
			        	<span>{this.props.pic.no}</span>
			        </div>
			        <div className="clearfix"></div>
			        <div className="pic-info">
				        <div className="stock"><span>{this.props.pic.stock} ()</span></div>
				        <div className="ingredient"><span>{this.props.pic.ingredient}</span></div>			        
			        </div>
			        <div className="buttons">
			        	<a href="#" className="btn btn-primary" role="button"></a>			        	
			        	<a href="javascript:;" style={{marginLeft:'6px'}} onClick={this.picDress} data-id={this.props.pic.id} data-src={this.props.pic.url} data-searchType="2" className="btn btn-primary" role="button"></a>
			        </div>
		        </div>
			</div>
		</div>
  	);
  }
}


